        justify-content: center;
        align-items: center;
        padding-top: 2%;
        padding-bottom: 2%;
      }
      .p-breadcrumb .p-breadcrumb-list li .p-menuitem-link .p-menuitem-text {
        color: #000;
        font-size: 16px;
        font-weight: 500;
        font-family: 'Inter', sans-serif;
      }
      .no__data__found {
        color: #b1b1b1;
        font-family: 'Inter', sans-serif;
        font-size: 18px;
