        font-size: 16px !important;
        font-weight: 500;
        line-height: 19px;
        letter-spacing: 0em;
        text-align: left;

    }
   
    .dropdown__container{
        width:100%;
        height:44px;
        border-radius: 12px;
        overflow: hidden;
        text-wrap: nowrap;
        margin: 10px 0px;
    }
  
