            background-color: #6366F1;
            height: 40px;
            width: 110px;
            border-radius: 30px;
            color: #fff;
            font-family: 'Inter', sans-serif;

            .add__icon__taxation {
                display: flex;
                justify-content: flex-end;
                align-items: flex-end;
                // padding-left: 10px;
                font-family: 'Inter', sans-serif;
            }

