        background-color: #6366f1;
        border-radius: 60px;
        width: 100px !important;
        height: 38px;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        color: #fff;
      }

      .filterbutton_container__taxation{
        display: flex;
