        border-radius: 10px;
        height: 45px;
        color:#B1B1B1;
    }
    .p-inputtext{
        border-color: transparent;
        height: 40px;
    }
    .p-inputtext:enabled:focus {
        outline: 0 none;
        outline-offset: 0;
        box-shadow: 0 0 0 0.2rem transparent;
        border-color: transparent;
    }
    .p-inputtext::placeholder {
        font-family: 'Inter', sans-serif;
        font-size: 14px;
        font-weight: 400;
        text-align: left;
        color: #B1B1B1;
        margin-left: 10px;
        
    }

    .p-placeholder {
        font-family: 'Inter', sans-serif;
        font-size: 14px;
        font-weight: 400;
        text-align: left;
        color: #B1B1B1;
        margin-left: 10px;
        
    }
    .sorbyfilter_container__taxation{
        flex-direction: row-reverse;
        // gap:10px;
        height: 46px;
        border-color: #d1d5db;
        width: 85%;
        margin-left: 8px;
        
    }
    .sort__by__text__taxation {
        font-family: 'Inter', sans-serif;
        font-size: 14px;
        font-weight: 500;
        text-align: left;
        color: #6366F1;

    }
    .pagedropdown_container{
        margin-right: 30%;
        border-style: hidden;
        background-color: #FAFAFA;
    }
    .header_search_container__taxation{
        display: flex;
        justify-content: space-between;
        align-items: center;
       margin-bottom: 16px;
    
    }
    .fieldvalue_container__taxation{
        font-size: 14px;
        font-family: 'Inter', sans-serif;
        font-weight: 400;
       
    }
    .fieldvalue_container.centered {
        display: flex;
        justify-content: center;
        align-items: center;
      }

}
