.quotelisting__overal__container {
    .quote__listing__card__table{
        margin-top: 1rem;
    }
    .feat_searchby_container{
        margin-top: 0px !important;
        border-radius: 10px;
        width: 100%;
    }
  .leadlisting__overal__container__title {
    font-family: Poppins !important;
    font-size: 34px !important;
    font-weight: 600 !important;
    line-height: 51px !important;
  }
  .breadCrums {
    margin: 16px 0px;
    border: none;
    color: #000;
    font-family: Poppins !important;
    font-size: 16px !important;
    font-weight: 400 !important;
    line-height: 24px !important;
  }

  .p-breadcrumb {
    border-radius: 6px;
    padding: 1rem 0rem;
  }
  .quote__listing__card__container {
    .p-card {
      background: #ffffff;
      color: #111927;
      box-shadow: none !important;
      border-radius: 20px !important;
      fill: #fff !important;
      filter: drop-shadow(0px 0px 20px rgba(0, 0, 0, 0.08)) !important;
      margin-top: 10px;
    }

    .p-tabview .p-tabview-nav li .p-tabview-nav-link {
      border: 0px #e5e7eb !important;
      border-width: 0 0 2px 0;
      border-color: transparent transparent #e5e7eb transparent;
      background: #ffffff;
      color: #6b7280;
      padding: 1.25rem;
      font-family: Poppins !important;
      font-size: 16px !important;
      font-weight: 400 !important;
      line-height: 24px !important;
      border-top-right-radius: 6px;
      border-top-left-radius: 6px;
      transition: box-shadow 0.2s;
      margin: 0 0 -2px 0;
    }

    .p-tabview .p-tabview-nav li.p-highlight .p-tabview-nav-link {
      background: #ffffff;
      border-color: #6366f1;
      color: #6366f1 !important;
    }

    .p-input-icon-left,
    .p-input-icon-right {
      position: relative;
      display: inline-block;
      width: 100%;
    }

    .p-inputtext {
      font-family: var(--font-family);
      font-feature-settings: var(--font-feature-settings, normal);
      font-size: 1rem;
      color: #4b5563;
      background: #ffffff;
      padding: 1rem 2.75rem;
    //   border: 1px solid #d1d5db;
      transition: background-color 0.2s, color 0.2s, border-color 0.2s,
        box-shadow 0.2s;
      appearance: none;
      border-radius: 10px;
      width: 100%;
    }
    .quote__listing__card__container__back__btn{
        display: flex;
        align-items: center;
        gap: 10px;
        .quote__listing__card__container__back__btn__title{
            font-size: 16px;
            font-weight: 400;
            font-family: Poppins;
            line-height: 24px;
            color: #111927;
        }
    }
    .btn__container__quote__listing{
        display: flex;
        justify-content: flex-end;
        width: 100%;
        .p-button-label {
            color: #fff;
            font-family: Poppins !important;
            font-size: 16px !important;
            font-weight: 400 !important;
            line-height: 24px !important;
          }  
          .p-button {
            margin: 0;
            display: inline-flex;
            cursor: pointer;
            user-select: none;
            align-items: center;
            vertical-align: bottom;
            text-align: center;
            overflow: hidden;
            position: relative;
            gap: 10px;
        }
        .p-button {
            color: #ffffff;
            background: #6366f1;
            border: 1px solid #6366f1;
            padding: 0.4rem 0.75rem !important;
            font-size: 1rem;
            transition: background-color 0.2s, color 0.2s, border-color 0.2s, box-shadow 0.2s;
            border-radius: 6px;
        }
    }
    .back__btn__container{
        display: flex;
        align-items: center;
    }
    .quote__listing__card__table{
        .header__btn__container {
            display: flex;
            gap: 15px;
        }

        .header__delete__btn {
            color: #000;
            font-family: Poppins !important;
            font-size: 16px !important;
            font-weight: 300 !important;
            line-height: 24px !important;
            cursor: default !important;
        }

        .header__edit__btn {
            color: #000;
            font-family: Poppins !important;
            font-size: 16px !important;
            font-weight: 300 !important;
            line-height: 24px !important;
            cursor: default !important;
        }
        .p-inputtext {
            font-family: var(--font-family);
            font-feature-settings: var(--font-feature-settings, normal);
            font-size: 1rem;
            color: #4b5563;
            background: #ffffff;
            padding: 0.75rem 0.75rem;
            // border: 1px 0px 1px 1px solid #d1d5db;
            transition: background-color 0.2s, color 0.2s, border-color 0.2s, box-shadow 0.2s;
            appearance: none;
            border-top-right-radius: 0px;
            border-top-left-radius: 6px;
            border-bottom-left-radius: 6px;
            border-bottom-right-radius: 0px;
            border-top: 0px solid #d1d5db !important;
            border-bottom: 0px solid #d1d5db !important;
            border-right: 0px solid #d1d5db !important;
            border-left: 0px solid #d1d5db !important;
            width: 100%;
        }

        .p-dropdown .p-dropdown-trigger {
            background: transparent;
            color: #6b7280;
            width: 3rem;
            border-top-right-radius: 6px;
            border-bottom-right-radius: 6px;
            border-top: 0px solid #d1d5db !important;
            border-bottom: 0px solid #d1d5db !important;
            border-right: 0px solid #d1d5db !important;
            border-left: 0px solid #d1d5db !important;
        }

        .p-dropdown {
            background: #ffffff;
            border: 0px solid #d1d5db;
            transition: background-color 0.2s, color 0.2s, border-color 0.2s, box-shadow 0.2s;
            border-radius: 6px;
            margin-top: 0.5rem !important;
        }

        .table__selector__text {
            color: #000 !important;
            font-family: Poppins !important;
            font-size: 14px !important;
            font-weight: 300 !important;
            line-height: 21px !important;
        }

        .p-paginator .p-paginator-current {
            background-color: transparent;
            border: 0 none;
            color: #000;
            min-width: 3rem;
            height: 3rem;
            margin: 0.143rem;
            padding: 0 0.5rem;
        }

        .p-paginator {
            background: #ffffff;
            color: #6b7280;
            border: 0px solid #f3f4f6;
            border-width: 0;
            padding: 0.5rem 1rem;
            border-radius: 6px;
        }
        .p-datatable .p-datatable-tbody>tr>td {
            text-align: left;
            border: 1px solid #e5e7eb;
            border-width: 0 0 1px 0;
            padding: 24px 1rem;
        }
        .company__title__text{
            color: #111927;
            font-family: Poppins !important;
            font-size: 16px !important;
            font-weight: 400 !important;
            line-height: 24px !important;
        }
        .company__id{
            color: #6C737F;
            font-family: Poppins !important;
            font-size: 14px !important;
            font-weight: 400 !important;
            line-height: 21px !important; 
        }
        .company__policy__type{
            color: #111927;
            font-family: Poppins !important;
            font-size: 14px !important;
            font-weight: 400 !important;
            line-height: 21px !important;
        }
        .company__status__type__green{
            background-color: #E2F6F0;
            color: #11845F;
            text-align: center;
            border-radius: 25px;
            padding: 8px;
            font-family: Poppins !important;
            font-size: 14px !important;
            font-weight: 400 !important;
            line-height: 16.94px !important;
        }
        .company__status__type__red{
            background-color: #FEF2E1;
            color: #C1622A;
            text-align: center;
            border-radius: 25px;
            padding: 8px;
            font-family: Poppins !important;
            font-size: 14px !important;
            font-weight: 400 !important;
            line-height: 16.94px !important;
        }
        .btn__container__view__edit {
            display: flex;
            justify-content: center;
            align-items: center;
        }
        .view__btn {
            color: #ffffff;
            background: transparent;
            border: 1px solid transparent !important;
            padding: 0.1rem 0.75rem !important;
            font-size: 1rem !important;
            transition: background-color 0.2s, color 0.2s, border-color 0.2s, box-shadow 0.2s;
            border-radius: 10px !important;
        }

        .edit__btn {
            color: #ffffff;
            background: transparent;
            border: 1px solid transparent !important;
            padding: 0.1rem 0.75rem !important;
            font-size: 1rem !important;
            transition: background-color 0.2s, color 0.2s, border-color 0.2s, box-shadow 0.2s;
            border-radius: 10px !important;
        }
    }
  }
}
